.disco-main {
    position: relative;
    width: 95%;
    height: 90%;
    background-color: black;
    border: solid;
    border-radius: 1em;
    @include flexColumn;
    h1 {
        margin: 1em 0;
        font-size: 2em;
    }
    
    .cover {
        z-index: -1;
        position: absolute;
        bottom: -5em;
        transform: translateX(-10vw);
        width: 15em;
        height: 15em;
        opacity: 0;
        transition: .8s;
        img {
            @include imageFit;
        }
    }
    .picto-cover {
        position: absolute;
        left: 0;
        flex: none;
        @include flexCenter;
        &::before {
            @include icon('\f51f',.8em);
        }
        &:hover .cover {
            @include flexCenter;
            z-index: 10;
            opacity: 1;
            transform: translateX(41vw);
        }
    }
    .filter-nav {
        display: flex;
        font-size: 1.5em;
        margin-top: .5em;
        select {
            color: black;
        }
        .filter-field {
            margin-right: 1em;
        }
        label {
            margin-right: .3em;
        }
    }
    .new-disc {
        position: absolute;
        top: .5em;
        right: .5em;
        font-size: 1.5em;
    }
    .discs-container {
        width: 95%;
        height: 68%;
        @include flexColumn;
        overflow: scroll;
    }
    thead,tbody,tr {
        width: 100%;
    }
    thead {
        margin-bottom: 1em;
    }
    tr {
        display: flex;
        align-items: center;
        position: relative;
    }
    th {
        flex: 2;
        font-size: 1.5em;
        @include flexCenter;
    }
    .selected {
        margin-left: .5em;
        &.hide {
            display: none;
        }
    }
    .selected.up::before {
        @include icon('\f0de');
    }
    .selected.down::before {
        @include icon('\f0dd');
    }

    td {
        flex: 2;
        @include flexCenter;
        margin: .2em 0;
        font-size: 1.2em;
    }
    .short {
        flex: 1;
    }
    .small {
        flex: none;
        width: 9vw;
    }
    .tiny {
        flex: none;
        width: 6vw;
    }
    .light-row {
        background-color: rgb(41, 41, 41);
    }
    .collector {
        position: absolute;
        font-size: 2em;
        right: 0;
    }
    .legend {
        position: absolute;
        bottom: .5em;
        left: 1em;
    }
}